* {
  padding: 0;
  margin: 0;
}

#displayBoxIMG, #displayBoxPDF {
    display: none;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
//Preview Box Container
.preview-box-container {
  position: fixed;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.888);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.selected_Image {
  width: 6cm;
  height: auto;
}

.close_Preview{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    width: 100%;
}
.preview-box {
  margin-top: 1cm;
  padding: .5cm;
  width: 10cm;
  border: 1px solid rgb(83, 83, 83);
  background-color: #ffffff;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.head-preview {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.buttonSend{
    padding:0.2cm 0.3cm;
    border-radius: 6px;
    background-color: rgb(171, 128, 236);
    color:#ffffff;
    font-size: 16px;
    border:none;
}
.App {
  height: 100vh;
  width: 100vw;
  overflow: auto;
}

.replying {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  column-gap: 2cm;
}

.close {
  width: .7cm;
  height: .8cm;
}

.chatParent {
  display: flex;
  flex-direction: column;
  align-items: space-between;
  justify-content: center;
  row-gap: 0.1cm;
}

.chatBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.mainChat {
  display: flex;
  flex-direction: column;
  border: thin solid rgb(157, 157, 157);
  row-gap: 0.2cm;
  border-radius: 6px;
  height: 12cm;
  width: 80vw;
  overflow: auto;
  scroll-behavior: smooth;
  padding-bottom: 0.2cm;
  padding-left: 0.2cm;
}

.userImg {
  width: 1cm;
  height: 1cm;
  border-radius: 50%;
}

.nameDiv {
  background-color: rgb(139, 244, 233);
  width: fit-content;
  padding: 0.1cm 0.3cm;
  border-radius: 6px;

}

.userName {
  font-size: 12px;
  color: rgb(91, 91, 91);
}

.leftMsg {
  display: flex;
  flex-direction: row;
  column-gap: 0.2cm;
  margin-top: 0.2cm;
}

.left {
  text-align: left;
  /* padding:0.5cm; */
}

.replymsgContainerRight {

  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
  column-gap: 0.1cm;

  &:hover {
    .icon1 {
      display: flex;
    }
  }
}

#file_name{
  font-size: 15px;
}

.imgMsg{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.repliedBoxRight {
  background-color: rgba(235, 199, 255, 0.956);
  padding: 0.1cm 0.2cm;
  border-radius: 12px;
}

.repliedToRight {
  background-color: rgba(218, 152, 254, 0.956);
  padding: 0.1cm 0.2cm;
  border-radius: 12px;
  border: 2px solid rgb(198, 127, 255);
}

.replymsgContainerLeft {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-end;
  column-gap: 0.1cm;

  &:hover {
    .icon1 {
      display: flex;
    }
  }

}

.repliedBoxLeft {
  // background-color: rgba(235, 199, 255, 0.956);
  // padding: 0.1cm 0.2cm;
  border-radius: 12px;


}

.repliedToLeft {
  background-color: rgb(70, 209, 195);
  padding: 0.1cm 0.2cm;
  border-radius: 12px;
  border: thin solid rgb(4, 138, 125);
}

.right {
  // text-align: right;
  background-color: rgba(235, 199, 255, 0.956);
  width: fit-content;
  padding: 0.1cm 0.3cm;
  border-radius: 6px;
}

.rightMsg {
  margin: 0.1cm;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
  column-gap: 0.1cm;
}

.msg {
  font-size: 14px;
  max-width: 50vw;
  word-break: break-all;
}

.icon {
  width: 0.7cm;
  height: 0.7cm;
}

.inputField {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  column-gap: 0.2cm;
}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0.4cm 1cm;
}

.signout {
  height: 1cm;
  width: 1cm;
  cursor: pointer;
}

.msgContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;

  &:hover {
    .icon1 {
      display: flex;
    }
  }
}

.tick {
  width: 0.3cm;
  height: 0.3cm;
}

.inputText {
  width: 20cm;
  padding: 0.3cm;
}

.pdfImage{
  width:2cm;
  height:2cm;
}
.replying {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 177, 214, 0.721);
  color: rgb(78, 78, 78);
  border: thin solid white;
  border-radius: 12px;
  padding: 0.2cm 0.4cm;
}

.close {
  width: 0.8cm;
  height: 0.8cm;
}

.icon1 {
  height: 0.5cm;
  width: 0.5cm;
  cursor: pointer;
  display: none;
}

.lmessage {
  display: flex;
  flex-direction: row;
  column-gap: 0.2cm;
  align-items: center;

  &:hover {
    .icon1 {
      display: flex;
    }
  }
}

.HomePage {
  width: 100vw;
  height: 95vh;
}

.mainContent {
  display: flex;
  flex-direction: row;
  justify-content: center;
  column-gap: 3cm;
  row-gap: 0.8cm;
  align-items: center;
  flex-wrap: wrap;
  padding: 1cm;
}

.mainLogo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 0.2cm;
}

.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 1cm;
  border: thin solid rgb(183, 183, 183);
  box-shadow: 1px 2px 0.5px grey;
  border-radius: 12px;
  width: fit-content;
  padding: 1cm;
}

.chatImage {
  width: 9cm;
  height: 9cm;
}

.SignInButton {
  padding: 0.2cm 0.3cm;
  background-color: rgb(48, 125, 250);
  color: #ffffff;
  box-shadow: 0.5px 0.5px black;
  border: none;
  border-radius: 6px;
}

.logo,
.userDiv {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  column-gap: 0.3cm;
}


.userNameNav {
  font-size: 16px;
}

.heading {
  font-size: 20px;
  font-weight: bolder;
}

.headingMain {
  font-size: 20px;
  font-weight: bolder;
}

.msgImage{
    height: auto;
    width: 5cm;
}

.ChatPage {
  height: 100vh;
}

.attachment {
  display: none;
}

.clip {
  width: 0.6cm;
  height: 0.6cm;
  cursor: pointer;
}

#previewBox, #previewBoxImage{
  display: none;
}

.noDecoration{
  text-decoration: none;
  color: #000;
}

#notAllowed{
  display: none;
  font-size: 15px;
  color:red;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding:0.2cm 0.1cm;
}

@media only screen and (min-width:940px) and (max-width:1040px) {
  .inputText {
    width: 18cm;
  }

  .mainContent {
    column-gap: 2cm;
  }
}

@media only screen and (min-width:840px) and (max-width:940px) {
  .inputText {
    width: 15cm;
  }

  .mainContent {
    column-gap: 1cm;
  }

  .chatImage {
    height: 8cm;
    width: 8cm;
  }
}

@media only screen and (min-width:680px) and (max-width:840px) {
  .inputText {
    width: 12cm;
  }

  .chatImage {
    height: 7cm;
    width: 7cm;
  }
}

@media only screen and (min-width:560px) and (max-width:680px) {
  .inputText {
    width: 10cm;
  }

  .msg {
    font-size: 13px;
  }

  .chatImage {
    height: 6cm;
    width: 6cm;
  }
}

@media only screen and (min-width:450px) and (max-width:560px) {
  .inputText {
    width: 8cm;
  }

  .nav {
    padding: 0.3cm 0.5cm;
  }

  .msg {
    font-size: 12px;
  }

  .mainChat {
    height: 13cm;
  }

  .chatImage {
    height: 5cm;
    width: 5cm;
  }

  .userName {
    font-size: 11px;
  }
}

@media only screen and (min-width:370px) and (max-width:450px) {
  .inputText {
    width: 6cm;
    padding: 0.2cm 0.3cm;
  }

  .heading {
    display: none;
  }

  .userNameNav {
    display: none;
  }

  .userName {
    font-size: 10px;
  }

  .userImg,
  .signout {
    height: 0.8cm;
    width: 0.8cm;
  }

  .icon {
    width: 0.5cm;
    height: 0.5cm;
  }

  .nav {
    padding: 0.2cm 0.4cm;
  }

  .mainChat {
    height: 12cm;
  }

  .msg {
    font-size: 11px;
  }

  .chatImage {
    height: 4cm;
    width: 4cm;
  }

  .headingMain {
    font-size: 20px;
  }

  .login {
    padding: 0.7cm;
  }
}

@media only screen and (max-width:370px) {
  .inputText {
    width: 4cm;
    padding: 0.2cm;
  }

  .heading {
    display: none;
  }

  .userNameNav {
    display: none;
  }

  .userName {
    font-size: 9px;
  }

  .userImg,
  .signout {
    height: 0.8cm;
    width: 0.8cm;
  }

  .icon {
    width: 0.5cm;
    height: 0.5cm;
  }

  .nav {
    padding: 0.2cm 0.2cm;
  }

  .mainChat {
    height: 12cm;
  }

  .msg {
    font-size: 11px;
  }

  .chatImage {
    height: 4cm;
    width: 4cm;
  }

  .headingMain {
    font-size: 16px;
  }

  .login {
    padding: 0.5cm;
  }
}